const { BehaviorSubject, from } = require('rxjs');
const { filter, map, switchMap } = require('rxjs/operators');
module.exports = async function (callback, dependencies) {
  let previousDeps = [];

  const shouldRun = () => {
    const newDeps = dependencies || [];
    return (
      newDeps.length !== previousDeps.length ||
      newDeps.some((dep, index) => dep !== previousDeps[index])
    );
  };

  const run = async () => {
    cleanup();
    effectCleanup = await callback();
  };

  const cleanup = () => {
    if (typeof effectCleanup === "function") {
     return effectCleanup();
    }
  };

  let effectCleanup = null;

  if (shouldRun()) {
    await run();
    previousDeps = dependencies || [];
  }

  return () => cleanup();
}
