const memoizedComponents = new Map();
const isEqual = require("fast-deep-equal");

function memo(Component) {
  if (memoizedComponents.has(Component)) {
    return memoizedComponents.get(Component);
  }

  function Memoized(props) {
    const condition =
      props?.key == 0 ? true : props?.key == null ? false : true;

    if (!Memoized.warned) {
      const condition =
        props?.key == 0 ? true : props?.key == null ? false : true;

      if (!condition) {
        console.warn(
          "Warning: Memoized component should always have a key. " +
            "This is especially important for components that have state or are prone to re-renders.",
          `@ ${Component?.name || ""}`
        );

        Memoized.warned = true;
      }
    }
    if (!Memoized.lastProps || !Memoized.areEqual(props, Memoized.lastProps)) {
      Memoized.lastProps = props;
      Memoized.lastResult = Component({ ...props, memo: true });
    }
    return Memoized.lastResult;
  }

  function areEqual(prevProps, nextProps) {
    if (prevProps === nextProps) {
      return true;
    }
    if (
      typeof prevProps !== "object" ||
      typeof nextProps !== "object" ||
      prevProps === null ||
      nextProps === null
    ) {
      return false;
    }
    const prevKeys = Object.keys(prevProps);
    const nextKeys = Object.keys(nextProps);
    if (prevKeys.length !== nextKeys.length) {
      return false;
    }
    for (const key of prevKeys) {
      if (
        !Object.prototype.hasOwnProperty.call(nextProps, key) ||
        !isEqual(prevProps[key], nextProps[key])
      ) {
        return false;
      }
    }
    if (!isEqual(prevProps?.children, nextProps?.children)) {
      return false;
    }
    return true;
  }

  Memoized.areEqual = areEqual;
  Memoized.lastProps = null;
  Memoized.lastResult = null;
  Memoized.warned = false;

  memoizedComponents.set(Component, Memoized);

  return Memoized;
}

module.exports = {
  memoizedComponents,
  memo,
};
