const { createData, memo } = require("../cjs/brace.development");

module.exports = Suspense;

const state = createData({
  isPending: true,
  error: null,
  resolvedChild: null,
});

function Suspense({ fallback, child, children, debounce, ...props }) {
 
  function resolveChildPromise() {
    (children || child)
      .then((result) => {
        state.set({
          resolvedChild: result,
          isPending: false,
        });
        console.log(result)
      })
      .catch((error) => {
        state.set({
          error,
          isPending: false,
        });
      });
  }

function render() {
  const { isPending, error, resolvedChild } = state();
   if (state().resolvedChild === null) {
      setTimeout(resolveChildPromise, debounce || 100);
      console.log(state())
   } 
    if (isPending) {
      return fallback;
    } else if (error) {
      alert(error);
      throw error;
    } else {
      return resolvedChild;
    }
 }
 
 //resolveChildPromise();

  return render();
}
