module.exports = function ({ each, children }) {
  if (!Array.isArray(each)) {
    throw new Error('For component expects an each prop.');
  }

  const elements = [];
  for (let i = 0; i < each.length; i++) {
    const item = each[i];
    const child = children(item, i);
    elements.push(child);
  }

  return elements;
}
