// Switch component

function Switch(props) {
  const { fallback } = props;
  const children = Array.isArray(props.children) ? props.children : [props.children];

  for (const child of children) {
    if (child) {
      return child;
    }
  }

  return fallback ? fallback : <comment># provide an additional fallback for UI</comment> ;
}

// Case component
function Case(props) {
  return [props.eval ? props.children : props.fallback ? props.fallback :
  <comment># provide an optional fallback for Case. Just Joking ðŸ˜‚ðŸ˜‚</comment>]
}

module.exports = {
  Switch,
  Case
}