'use strict';
const { setLogLevel, setTopMessage } = require("./haystack");


if (process.env.NODE_ENV !== 'production') {
{
  function initDev() {
    setLogLevel("debug");
    setTopMessage(
      `You are running a development build of BraceJs.\n` +
        `production build will be used automatically when deploying to browser when deploying for production.`,
      "warning"
    );
  }
  initDev();
}

  module.exports = {
    ...require('./cjs/brace.development.js'),
    ...require('./controls')
  }
} else {
  module.exports = require('./cjs/brace.production.js');
}

